.barChartMining {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column: span 2;
  width: 100%;
  @include contentWidth;
  margin-bottom: 5rem;
}

@media (max-width: 1680px) {
  .barChartMining {
    grid-template-columns: 65% 1fr;
  }
}

@media (max-width: 767px) {
  .barChartMining {
    grid-template-columns: 1fr;
  }
}