.mapTransport {
  position: relative;
  min-height: 107rem;

  &__title {
    position: relative;
    z-index: 1;
    padding: 0 0 0 13rem;
  }

  &__map {
    position: absolute;
    top: 6rem;
    left: -12rem;
  }

  &__mapPart {
    position: absolute;
    z-index: 1;
    opacity: 0;
    transition: opacity $eia250;

    &--big {
      top: 23rem;
      left: 6rem;
    }

    &--small {
      top: 28.6rem;
      left: 62.6rem;
    }

    &--percentage {
      bottom: 10rem;
      width: 192rem;
      left: -12rem;
      opacity: 1;
    }

    &.active {
      opacity: 1;
    }
  }

  &__toggle {
    position: absolute;
    z-index: 2;
    top: 38rem;
    left: 104rem;
    width: 9rem;
    height: 26rem;
    background-color: transparent;
  }
}

@media (max-width: 1680px) {
  .mapTransport {
    min-height: 80rem;

    &__map {
      width: 144rem;
      top: 0;
    }

    &__mapPart {
      &--big {
        top: 12.6rem;
        left: 0.5rem;
        width: 110rem;
      }

      &--small {
        top: 18.6rem;
        width: 58.2rem;
        left: 48.4rem;
      }

      &--percentage {
        bottom: 11.5rem;
        width: 144rem;
      }
    }

    &__toggle {
      top: 24rem;
      left: 74rem;
      width: 7rem;
      height: 20rem;
    }
  }
}

@media (max-width: 767px) {
  .mapTransport {
    display: none;
  }
}