.header {
  &__wrapper {
    position: fixed;
    z-index: 15;
    top: 0;
    left: 0;
    right: 0;
    min-height: 19rem;
    backdrop-filter: blur(30px);
    background-color: rgba($white, .8);
    box-shadow: 0 0 1rem 0 #e4e4e4;

    &.home {
      min-height: 17rem;
      box-shadow: none;
    }
  }

  &__content {
    position: fixed;
    top: 0;
    left: -1rem;
    right: -1rem;
    z-index: 50;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 1.5rem 0 2rem;
    margin: 0 auto;
    max-width: 172rem;
    backdrop-filter: blur(3rem);
  }

  &__logo-wrapper {
    &.active {
      pointer-events: none;
    }
  }

  &__logo {
    width: 40rem;
    height: 9rem;
    margin: 0 0 0 -.5rem;
  }


  &__nav-box {
    display: flex;
    justify-content: space-between;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5rem;
  }

  &__nav-item-wrapper {
    position: relative;
    margin: 0 0 0 3.4rem;
  }


  &__nav-item {
    position: relative;
    transition: color $eia200;

    &:hover,
    &.active {
      color: $purple;
    }

    &.active {
      cursor: default;
    }
  }


  &__nav-value {
    .header__nav-item.active & {
      pointer-events: none;
    }
  }

  &__toggle {
    position: absolute;
    top: 2.5rem;
    left: calc(50% - .4rem);
    display: block;
    width: 2rem;
    height: 2rem;
    border-top: .2rem solid $black;
    border-left: .2rem solid $black;
    transform: rotate(-135deg) translateX(50%);
    opacity: 1;
    transition: opacity $eia250;

    &.opened {
      opacity: 0;
      pointer-events: none;
    }
  }

  &__mobToggle {
    display: none;
  }
}

@media (max-width: 1680px) {
  .header {
    &__wrapper {
      min-height: 15rem;

      &.home {
        min-height: 15rem;
      }
    }

    &__content {
      max-width: 124rem;
      padding: 0 2rem;
    }
  }
}

@media (max-width: 767px) {
  .header {
    min-height: auto;

    &__content {
      padding: 2.7rem 2.2rem 4rem;
    }

    &__logo-wrapper {
      position: absolute;
      z-index: 1;
      top: 1.5rem;
      left: 2rem;
    }

    &__logo {
      width: 20rem;
      height: 4rem;
      margin: 0;
    }

    &__mobToggle {
      position: relative;
      z-index: 3;
      display: inline-block;
      vertical-align: middle;
      width: 2.8rem;
      height: 2.8rem;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: .2rem;
        background-color: $black;
        transition: transform 0ms ease;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: .2rem;
        left: 0;
        width: 40%;
        height: .2rem;
        background-color: $black;
        transition: transform 0ms ease;
      }

      &.opened {
        &::before {
          transform: rotate(45deg);
          transform-origin: left;
          width: 3.4rem;
          transition: transform $eia250;
        }

        &::after {
          transform: rotate(-45deg);
          transform-origin: left;
          width: 3.4rem;
          transition: transform $eia250;
        }
      }
    }

    &__mobToggle-line {
      position: absolute;
      top: 1.2rem;
      left: 0;
      width: 100%;
      height: .2rem;
      background-color: $black;

      .header__mobToggle.opened & {
        display: none;
      }
    }

    &__nav-box {
      position: relative;
      width: 100%;
      justify-content: flex-end;
    }

    &__nav {
      display: none;
      position: absolute;
      z-index: 2;
      top: -2.7rem;
      left: -2.2rem;
      width: 100vw;
      height: 100vh;
      flex-direction: column;
      justify-content: flex-start;
      padding: 12rem 2rem;
      background-color: $white;

      .header__mobToggle.opened + & {
        display: flex;
      }
    }

    &__nav-item {
      display: flex;
      align-items: flex-end;
      width: 100%;
      height: 5.6rem;
      padding: 0 0 1rem;
      margin: 0;
      border-bottom: .1rem solid $black;

      &::after {
        content: '';
        position: absolute;
        right: 1.4rem;
        top: 3rem;
        width: 1.2rem;
        height: 1.2rem;
        transform: rotate(45deg);
        border-right: .1rem solid black;
        border-top: .1rem solid black;
      }
    }

    &__btnDownload.button {
      display: none;
      position: absolute;

      .header__mobToggle.opened ~ & {
        z-index: 2;
        top: 46.8rem;
        left: 3rem;
        display: inline-flex;
        vertical-align: middle;
        width: 28rem;
        height: 5rem;
        align-items: center;
        justify-content: center;
        font-size: $fz-m;
      }
    }

    &__toggle {
      display: none;
    }
  }
}