.pieChart {
  &__title {
    margin: 0 0 3.6rem;
  }

  &__content {
    display: grid;
    grid-template-columns: 45rem 68rem;
    grid-gap: 0 2rem;

    .pieChart__tabsLeft & {
      grid-template-columns: 30rem 30rem 68rem;
    }
  }

  &__chartBox {
    position: relative;
  }

  &__chart {
    width: 32rem;
    height: 32rem;
    pointer-events: none;
  }

  &__label {
    position: absolute;
    top: 12rem;
    left: 9.5rem;
    font-size: $fz-xxl;
    line-height: 5.5rem;
    font-weight: 700;

    .pieChart__tabsLeft & {
      left: auto;
      right: 8rem;
    }
  }

  &__buttonBox {
    display: inline-flex;
    vertical-align: middle;
    grid-column: span 2;
    align-items: center;
    margin: 0 0 1.6rem 2rem;
    border-radius: .5rem;

    .pieChart__tabsLeft & {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      grid-column: span 1;
      margin: 5rem 0 1.6rem 2rem;
    }
  }

  &__button.button {
    padding: 0 4rem;
    color: $black;
    background-color: $gray4;

    &:hover {
      opacity: 1;
    }

    &.active {
      color: $white;
      background-color: $black;
      cursor: default;
    }

    .pieChart__tabsLeft & {
      width: 26rem;
      height: 7.2rem;
      margin: 0 0 1rem;
      padding: 1rem 4rem 1rem 1rem;
      text-align: left;
      font-size: $fz-m;
    }
  }

  &__dataWrapper {
    padding: 9rem 0 0;

    .pieChart__tabsLeft & {
      display: grid;
      grid-template-columns: 15rem 1fr;
    }
  }

  &__dataShift {
    margin: 0 0 .5rem;
    font-size: $fz-xxl;
    line-height: 5.5rem;
    letter-spacing: .054rem;
    font-weight: 700;
  }

  &__dataMeasurement,
  &__dataDesc {
    font-size: $fz-s;
    line-height: 2rem;
  }

  &__dataDesc {
    &--mobile {
      display: none;
    }
  }

  &__dataMeasurement {
    display: block;
    margin: 0 0 4.5rem;
  }

  &__buttonDesc {
    margin: 1.5rem 0 0;
  }
}

@media (max-width: 1680px) {
  .pieChart {
    &__content {
      grid-template-columns: 40rem 68rem;

      .pieChart__tabsLeft & {
        grid-template-columns: 30rem 30rem 60rem;
      }
    }

    &__dataWrapper {
      padding: 5rem 0 0;
    }
  }
}

@media (max-width: 767px) {
  .pieChart {
    &__content {
      grid-template-columns: 20rem 15rem;
    }

    &__chart {
      width: 20rem;
      height: 20rem;
    }

    &__dataDesc {
      display: none;

      &--mobile {
        display: block;
        grid-column: span 2;
        font-size: 1.2rem;
      }
    }

    &__buttonBox {
      margin: 0;
    }

    &__button.button {
      padding: 0 1rem;
      font-size: 1.2rem;
    }

    &__label {
      top: 13.5rem;
      left: 6.5rem;
      font-size: 2.5rem;
      line-height: 1;
    }

    &__dataShift {
      font-size: 2.5rem;
      line-height: 1;
    }

    &__dataMeasurement {
      font-size: 1.2rem;
    }

    &__dataWrapper {
      padding: 9rem 0 0;
    }
  }
}
