.partsContent {
  display: grid;
  grid-template-columns: 24rem 1fr;
  width: 100%;

  &--widerLeft {
    grid-template-columns: 44rem 1fr;
  }

  &__left {
    &.sole {
      grid-column: span 2;
      max-width: 54rem;
    }

    .partsContent.full & {
      max-width: none;
    }
  }

  &__right {
    position: relative;
    padding: 0 0 0 8rem;
    margin: 0 0 0 10rem;

    &::before {
      content: '';
      position: absolute;
      top: .5rem;
      bottom: .5rem;
      left: 0;
      width: .3rem;
      background-color: $gray2;
    }

    .partsContent.noLine & {
      &::before {
        display: none;
      }
    }
  }

  h3 {
    margin: 0 0 1.5rem;
    font-size: $fz-xl;
    line-height: 3.5rem;
  }

  h5 {
    font-size: $fz-s;
    line-height: 2rem;
  }

  .underlined {
    padding: 0 0 2rem;
    border-bottom: .3rem solid $gray2;
  }

  p {
    font-size: $fz-s;
    line-height: 2rem;
  }

  .gray {
    color: $gray2;
  }

  .titleBox {
    margin: 0 0 2rem;

    &:last-of-type {
      margin: 0;
    }
  }

  .innerParts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0 15rem;
    width: 100%;

    &--closer {
      grid-template-columns: 30rem 30rem;
      grid-gap: 0 10rem;
    }
  }
}

@media (max-width: 767px) {
  .partsContent {
    grid-template-columns: 1fr;
    padding: 0 0 1rem;

    &__left {
      padding: 0 0 1rem;
    }

    &__right {
      padding: 0 0 1rem;
      margin: 0;
      border-top: .1rem solid $black;
      border-bottom: .1rem solid $black;

      &::before {
        display: none;
      }
    }

    h3 {
      margin: 2rem 0 .5rem;
      font-size: $fz-m;
      line-height: 1;
    }

    p {
      font-size: 1.2rem;
      line-height: 1.25;
    }
  }
}