.gasPage {
  position: relative;

  &__sectionTitle-box {
    display: grid;
    grid-template-columns: 100rem 1fr;
    @include contentWidth;
    margin-bottom: 5rem;
    align-items: baseline;
    justify-items: flex-end;
    padding: 9rem 0 0;
    border-top: .1rem solid $purple;

    &--assets {
      padding: 25rem 0 0;
    }
  }

  &__sectionTitle {
    @include contentWidth;
    min-height: 5.5rem;
    margin: 0 auto 6.5rem;
    font-size: $fz-xxxl;
    line-height: 7.5rem;
    color: $black;
    text-transform: uppercase;
    letter-spacing: 1.03rem;
    transition: font-size $eia200;

    .gasPage__sectionTitle-box & {
      margin: 0;
      justify-self: flex-start;

      &--supply {
        margin: 0 0 12rem;
      }
    }

    .gasPage__sectionTitle-box--assets & {
      grid-column: span 2;
      max-width: 120rem;
    }

    &.active {
      position: sticky;
      top: 11.5rem;
      left: 0;
      z-index: 15;
      font-size: 4.5rem;
      line-height: 1;
    }
  }

  &__stickyNav {
    &.active {
      position: sticky;
      top: 13.6rem;
      left: 0;
      z-index: 15;
    }
  }

  &__tabsContent {
    grid-column: span 2;
  }

  &__wide {
    grid-column: span 2;
  }

  &__content {
    padding: 0 0 0 25rem;

    &.gasPage__sectionField {
      margin: 0 0 4rem;
    }
  }

  &__innerTabs-content {
    position: relative;
  }

  &__subTitle {
    @include contentWidth;
    display: grid;
    grid-template: 1fr 1fr/ fit-content(10rem) 1fr;
    grid-gap: 3rem 2rem;
    align-items: baseline;
    padding: 0 0 0 1rem;

    &--prices {
      padding: 10rem 0;
    }
  }

  &__subTitle-amount {
    font-size: $fz-xxxxl;
    letter-spacing: .083rem;
    color: $purple;
  }

  &__subTitle-value {
    font-size: $fz-xl;
    line-height: 4rem;
    color: $black;
    text-transform: uppercase;
    font-weight: 400;
  }

  &__subTitle-desc {
    font-size: $fz-s;
    line-height: 2rem;
    grid-column: 2/3;
  }

  &__innerTitle {
    padding: 15rem 0 0;
    margin: 0 0 2rem;
  }

  &__innerTabs {
    margin: 1rem 0 1.5rem 13rem;
  }

  &__tabContent {
    max-width: 120rem;
    width: 100%;
  }

  &__lineChart {
    padding: 4rem 0 0 12.5rem;
    margin: 0 0 4rem;
  }

  &__partsContent {
    margin: 3rem 0;

    &--prices {
      max-width: 130rem;
    }
  }

  &__productionParts {
    width: 100rem;
    padding: 6rem 0 0;
    margin: 0 0 7rem;
  }

  &__pieChartProduction {
    width: 100%;
    margin: 0 0 9rem;
  }

  &__leftItem {
    justify-self: flex-start;
  }

  &__leftPadding {
    padding-left: 13rem;
  }

  &__outputList {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10rem 15rem;
    width: 100%;
    padding: 10rem 0 0 13rem;
    margin: 0 0 10rem;
  }
}

@media (max-width: 1680px) {
  .gasPage {
    &__content {
      padding: 0;
    }

    &__innerTabs {
      margin: 0 0 1.5rem;
    }

    &__innerTitle {
      padding: 6rem 0 0;
    }

    &__sectionTitle-box {
      &--mining,
      &--production,
      &--supply {
        grid-template-columns: 65rem 1fr;
        padding: 6rem 0 0;
      }

      &--assets {
        padding: 16rem 0 0;
      }
    }

    &__sectionTitle {
      margin: 0 auto 6.5rem;
      line-height: 1;

      .gasPage__sectionTitle-box & {
        margin: 0 0 5rem;
      }

      &.active {
        top: 8.5rem;
      }
    }

    &__leftPadding {
      padding-left: 0;
    }

    &__stickyNav {
      margin: 0 -1rem 0 0;

      &.active {
        top: 10.5rem;
      }
    }

    &__outputList {
      grid-template-columns: repeat(3, 1fr);
      justify-self: self-start;
      grid-gap: 7.5rem 13rem;
      max-width: 105rem;
      padding: 4rem 0 0;
    }

    &__assetsMap {
      width: 105rem;
    }
  }
}

@media (max-width: 767px) {
  .gasPage {
    &__sectionTitle-box {
      grid-template-columns: 1fr;
      margin: 0 2.5rem;
    }

    &__sectionTitle {
      padding: 0 0 4rem 2.5rem;
      margin: 0 auto 2rem;
      font-size: 2rem;
      line-height: 1;
      letter-spacing: normal;

      .gasPage__sectionTitle-box & {
        padding: 0 0 4rem;
      }
    }

    &__subTitle {
      grid-gap: 1rem 1rem;
      width: 100vw;
      padding: 0 2.5rem;
      margin: 0 0 3rem;
    }

    &__innerTitle {
      padding: 3rem 0 0;
      margin: 0 0 3.5rem;
    }

    &__innerTabs {
      margin: 0 0 1.5rem;
    }

    &__subTitle-amount {
      font-size: 2.5rem;
    }

    &__subTitle-value {
      font-size: 1.4rem;
      text-transform: none;
    }

    &__subTitle-desc {
      font-size: 1.2rem;
      line-height: 1.25;
      grid-column: 1/3;
    }

    &__wide {
      grid-column: 1;
    }

    &__stickyNav {
      overflow-x: scroll;
      display: flex;
      width: 100%;
      align-items: center;
      margin: 0;
    }

    &__leftItem {
      grid-column: 1;
    }
  }
}