.output {
  &__link {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: left;

    &:disabled {
      color: $black;
      cursor: default;
    }
  }

  &__title-box {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: flex-start;
    grid-column: span 3;
    padding: 0 0 .8rem;
    margin: 0 0 1.2rem;
    border-bottom: .2rem solid $gray2;

    .output.sole & {
      min-height: 5.8rem;
    }

    .output__tabs ~ .output__tab-content & {
      padding: 0 0 .6rem;
    }
  }

  &__tabs {
    margin: 1.1rem 0;
  }

  &__title {
    padding: 0 1.5rem;
    font-size: $fz-m;
    line-height: 1.75;
    font-weight: 400;
    background-color: $gray4;
    transition: background-color $eia200, color $eia200, border-radius $eia200;

    &:nth-child(2n + 1) {
      border-radius: .5rem 0 0 .5rem;
    }

    &:nth-child(2n) {
      border-radius: 0 .5rem .5rem 0;
    }

    &.active {
      background-color: $black;
      color: $white;
      border-radius: .5rem;
      font-weight: 600;
      transition: background-color 0ms, color 0ms, border-radius 0ms;
    }

    .output.sole & {
      align-self: flex-end;
      padding: 0;
      background-color: transparent;
    }

    .output.small & {
      font-size: $fz-m;
      line-height: 2.4rem;
    }
  }

  &__tab-content,
  &__sole-content {
    display: grid;
    width: 100%;
    text-align: left;
  }

  &__sole-content {
    grid-template-columns: 15rem 1fr 1fr;
  }

  &__value-box {
    display: flex;
    flex-direction: column;
  }

  &__value {
    font-size: $fz-xxl;
    font-weight: 700;
    line-height: 1;

    .output.small & {
      font-size: $fz-xl;
    }
  }

  &__percentage {
    justify-self: flex-end;
    padding: .8rem .8rem 0 0;
    color: $red;
    font-size: 3rem;
    font-weight: 700;

    .output.small & {
      grid-column-start: 3;
      font-size: $fz-m;
    }

    &.positive {
      color: $green;
    }
  }
}

@media (max-width: 1680px) {
  .output {
    &__percentage {
      grid-column-start: 3;
    }
  }
}

@media (max-width: 767px) {
  .output {
    &:not(.sole) {
      pointer-events: none;
    }

    &__tabs {
      margin: 0;

      li:not(:first-child) {
        display: none;
      }
    }

    &__title {
      font-size: 1.4rem;
      line-height: 1.25;
    }

    &__title-box {
      grid-column: span 2;

      .output.sole & {
        min-height: 5.2rem;
        padding: 0 0 4px;
      }

      .output__tabs ~ .output__tab-content & {
        padding: 0 0 .4rem;
      }
    }

    &__value {
      font-size: 2.5rem;
    }

    &__percentage {
      grid-column-start: 2;
      font-size: $fz-m;
    }

    &__sole-content,
    &__link {
      grid-template-columns: 8rem 1fr;
    }

    &__measurement {
      font-size: 1.2rem;
    }
  }
}