.chartDescPrev {
  display: flex;
  flex-direction: column;
  padding: 8.5rem 0 0;

  &__title {
    margin: 0 0 1rem;
    font-size: $fz-s;
    line-height: 2rem;
  }

  &__value,
  &__shift {
    font-size: $fz-xxl;
    line-height: 5.5rem;
  }

  &__shift {
    margin: 0 0 1.5rem;
  }

  &__measurement {
    font-size: $fz-s;
    line-height: 2rem;
  }

  &__desc {
    padding: 6rem 0 0;
    font-size: $fz-s;
    line-height: 2rem;
  }
}

@media (max-width: 1680px) {
  .chartDescPrev {
    .barChartMining & {
      padding: 6.5rem 8rem 0 0;
    }
  }
}

@media (max-width: 767px) {
  .chartDescPrev {
    display: block;
    grid-column-start: 1;

    .barChartMining & {
      padding: 2.5rem 0 0;
    }

    &__title,
    &__measurement {
      font-size: 1.2rem;
    }

    &__title {
      margin: 0 0 0.5rem;
    }

    &__shift,
    &__value {
      font-size: 2.5rem;
      line-height: 1;
    }

    &__shift {
      margin: 0 3.5rem 0 0;
    }

    &__value {
      margin: 0 .5rem 0 0;
    }

    &__desc {
      padding: 0;
    }
  }
}