.mapRegionsStructure {
  position: relative;
  min-height: 90rem;

  &__title {
    position: relative;
    z-index: 1;
    padding: 0 0 0 13rem;
  }

  &__map {
    position: absolute;
    top: -.5rem;
    left: -12rem;
    width: 192rem;
  }

  &__region {
    position: absolute;
    cursor: pointer;

    &--green {
      top: 9.8rem;
      left: 43.5rem;
    }

    &--purple {
      top: 12.2rem;
      left: 2.3rem;
      width: 50.5rem;
      height: 65.8rem;
    }

    &--teal {
      top: 22.6rem;
      left: 30.2rem;
      width: 64.6rem;
      height: 59.8rem;
    }

    &--orange {
      top: 22.7rem;
      left: 82.3rem;
      width: 78.3rem;
      height: 62.25rem;
    }

    &--active {
      opacity: 0;
      transition: opacity $eia250;

      &:hover {
        opacity: 1;
      }

      .mapRegionsStructure__region--green:hover + &,
      .mapRegionsStructure__region--purple:hover + & {
        opacity: 1;
      }
    }
  }
}

@media (max-width: 1680px) {
  .mapRegionsStructure {
    min-height: 67.2rem;

    &__map {
      width: auto;
      height: 67.2rem;
    }

    &__title {
      padding: 0 0 0 10rem;
    }

    &__region {
      &--purple {
        top: 8.8rem;
        left: -1.3rem;
        width: 38rem;
        height: 50rem;
      }

      &--teal {
        top: 9.4rem;
        width: 48.6rem;
        left: 19.7rem;
      }

      &--orange {
        top: 9.2rem;
        left: 58.9rem;
        width: 59rem;
      }
    }
  }
}

@media (max-width: 767px) {
  .mapRegionsStructure {
    display: none;
  }
}