.tabs {
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  background-color: $gray4;
  border-radius: .5rem;

  &.link {
    background-color: transparent;
    border-radius: 0;
  }

  &__item {
    min-height: 2.8rem;
    padding: 0 2rem;
    font-size: $fz-m;
    line-height: 2rem;

    &.active {
      background-color: $black;
      color: $white;
      border-radius: .5rem;
      transition: color $eia200;

      .link & {
        color: $blue;
        background-color: transparent;
        border-radius: 0;
      }
    }
  }

  &__hint {
    justify-self: flex-start;
    padding: 0 0 0 13rem;
    font-size: $fz-xs;
    line-height: 1.7rem;
    color: $gray2;
  }
}

@media (max-width: 1680px) {
  .tabs {
    &__item {
      padding: 0 1rem;
    }

    &__hint {
      padding: 0;
    }
  }
}

@media (max-width: 767px) {
  .tabs {
    &.output__tabs {
      min-height: 4.6rem;
      align-items: flex-end;
      background-color: transparent;
    }

    &__itemWrapper {
      .link & {
        margin: 0 1rem 0 0;

        &:last-of-type {
          margin: 0;
        }
      }
    }

    &__item {
      font-size: 1.4rem;

      .output__tabs & {
        min-height: auto;
        padding: 0;
        line-height: 1.25;
        color: $black;
        background-color: transparent;
      }

      .link & {
        font-size: 1.2rem;
        white-space: nowrap;
        background-color: $gray4;
        border-radius: .5rem;

        &.active {
          background-color: $black;
          color: $white;
          border-radius: .5rem;
          transition: color $eia200;
        }
      }
    }
  }
}