.mapShelf {
  position: relative;
  min-height: 100rem;

  &__title {
    position: relative;
    z-index: 1;
    padding: 0 0 0 13rem;
  }

  &__map {
    position: absolute;
    top: -.5rem;
    left: -12rem;
    width: 192rem;
  }

  &__region {
    position: absolute;
    cursor: pointer;
    transition: opacity $eia250;

    &--baltic {
      width: 25rem;
      top: 3.8rem;
      left: 8.3rem;
    }

    &--black {
      width: 27rem;
      top: 32.2rem;
      left: -6.8rem;
    }

    &--caspian {
      top: 61rem;
      width: 32rem;
      left: 11.3rem;
    }

    &--pech {
      top: 13.7rem;
      left: 52.8rem;
      width: 20.2rem;
    }

    &--ohotsk {
      top: 43rem;
      left: 129rem;
      width: 22rem;
    }

    &--active {
      z-index: 1;
      opacity: 0;

      &.mapShelf__region--baltic {
        width: 23rem;
        top: 2.4rem;
      }


      &.mapShelf__region--black {
        width: 16rem;
        top: 30rem;
        left: -4.5rem;
      }

      &.mapShelf__region--caspian {
        width: 23.2rem;
      }

      &.mapShelf__region--pech {
        top: 11rem;
        width: 16rem;
        left: 52.4rem;
      }

      &.mapShelf__region--ohotsk {
        top: 42.3rem;
        left: 128.9rem;
      }

      &:hover {
        opacity: 1;

        & + .mapShelf__region--baltic,
        & + .mapShelf__region--black,
        & + .mapShelf__region--caspian,
        & + .mapShelf__region--pech,
        & + .mapShelf__region--ohotsk {
          opacity: 0;
        }
      }
    }
  }
}

@media (max-width: 1680px) {
  .mapShelf {
    min-height: 75rem;

    &__map {
      width: 144rem;
    }

    &__region {
      &--caspian {
        top: 45rem;
        left: 5.5rem;
        width: 25rem;
      }

      &--black {
        top: 23.5rem;
        left: -8rem;
        width: 21rem;
      }

      &--baltic {
        top: 2rem;
        left: 2.8rem;
        width: 20rem;
      }

      &--pech {
        top: 9.7rem;
        left: 36.5rem;
        width: 16.2rem;
      }

      &--ohotsk {
        top: 32rem;
        left: 93rem;
        width: 17rem;
      }

      &--active {
        &.mapShelf__region--caspian {
          width: 18.2rem;
        }

        &.mapShelf__region--black {
          top: 22rem;
          left: -6.2rem;
          width: 12.2rem;
        }

        &.mapShelf__region--baltic {
          width: 20rem;
          top: 0;
          left: 2.7rem;
        }

        &.mapShelf__region--pech {
          top: 7.6rem;
          left: 36.2rem;
          width: 12.5rem;
        }

        &.mapShelf__region--ohotsk {
          top: 31.5rem;
          left: 93rem;
          width: 17rem;
        }
      }
    }
  }
}