.barChartField {
  display: grid;
  grid-template-columns: 65rem 1fr;
  @include contentWidth;

  &__mobile {
    display: none;
  }
}

@media (max-width: 1680px) {
  .barChartField {
    grid-template-columns: 65rem 1fr;
  }
}

@media (max-width: 767px) {
  .barChartField {
    grid-template-columns: 100vw;

    &__chart {
      display: none;
    }

    &__mobile {
      display: block;
      padding: 0 2.5rem;
    }

    &__mobile-lineBox {
      display: flex;
      align-items: center;
    }

    &__mobileLine {
      height: 1.6rem;
      margin: 0 1rem 0 0;
    }

    &__mobileValue {
      font-weight: 700;
    }

    &__mobile-buttonBox {
      overflow-x: scroll;
      display: flex;
      align-items: center;
      padding: 0 0 2rem;
      margin: 0 0 1rem;
    }

    &__mobileButton {
      height: 3.4rem;
      padding: 0 1rem;
      margin: 0 1rem 0 0;
      color: $white;
      font-size: 1.4rem;
      white-space: nowrap;
      font-weight: 700;
      border-radius: .5rem;

      &:last-of-type {
        margin: 0;
      }
    }
  }
}