.barDescField {
  padding: 5rem 0 0;

  &__amount {
    margin: 0 0 1rem;
    font-size: $fz-xxxxl;
    letter-spacing: .083rem;
    font-weight: 700;
  }

  &__desc {
    font-size: $fz-s;
    line-height: 2rem;
    margin: 0 0 4rem;
  }

  &__shift {
    font-size: $fz-xxl;
    line-height: 5.5rem;
    letter-spacing: .054rem;
    font-weight: 700;
    margin: -2rem 0 .5rem;
  }
}

@media (max-width: 1680px) {
  .barDescField {
    &__desc {
      min-height: 4rem;
      margin: 0 0 2rem;
    }
  }
}

@media (max-width: 767px) {
  .barDescField {
    display: grid;
    grid-template-columns: 3rem auto;
    grid-gap: 1.5rem 0;
    align-items: baseline;
    padding: .5rem 2.5rem;

    &__amountBox {
      display: flex;
      align-items: baseline;
      grid-column: span 2;
    }

    &__amount {
      margin: 0 .2rem 0 0;
      font-size: 2.5rem;
    }

    &__desc {
      margin: 0;
      font-size: 1.2rem;
      grid-column-start: 2;

      &--long {
        grid-column-start: 1;
        grid-column: span 2;
        padding: .5rem 0 0;
        font-size: 1rem;
        color: $gray2;
      }
    }

    &__shiftBox {
      display: flex;
      align-items: baseline;
    }

    &__shift {
      margin: 0 .2rem 0 0;
      font-size: $fz-m;
      line-height: 1;
    }
  }
}