.heading {
  color: $purple;
  font-size: $fz-xl;
  line-height: 5rem;
  text-transform: uppercase;

  &.small {
    font-size: $fz-m;
    line-height: 2.4rem;
    color: $black;
    text-transform: none;
  }

  &__sub {
    padding: 1rem 0 0;
    font-size: $fz-xs;
  }
}

@media (max-width: 1680px) {
  .heading {
    .pieChartProduction + & {
      grid-column: span 2;
    }
  }
}

@media (max-width: 767px) {
  .heading {
    font-size: $fz-m;
  }
}