.main {
  position: relative;
  @include contentWidth;

  &__upper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column: span 2;
    margin: 0 0 25rem;
  }

  &__header-box {
    position: relative;
    z-index: 1;
    padding: 14rem 0 0 4rem;
  }

  &__header {
    font-size: 26.5rem;
    color: $black2;
  }

  &__subheader {
    font-size: 17.6rem;
    line-height: 1;
    color: $black2;
  }

  &__desc {
    padding: 3.5rem 0 0;
    font-size: 4.1rem;
    font-weight: 400;
    color: $black2;
  }

  &__year {
    position: relative;
    z-index: 1;
    padding: 0 3.5vw 0 0;
    grid-column-start: 2;
    grid-row-start: 1;
    text-align: right;
    color: rgba($gray, 0.9);
    letter-spacing: 0;
    font-size: 47rem;
    line-height: 42rem;
    font-weight: 700;
  }

  &__map {
    position: absolute;
    z-index: 0;
    top: 5rem;
    right: -3rem;
    max-width: 128rem;
  }

  &__btn-nav {
    position: relative;
    align-self: self-start;
    justify-self: start;
    margin: 0 0 0 4rem;
    color: $gray3;
    font-size: $fz-m;
    letter-spacing: 0.1rem;
    transition: opacity $eia200;

    &:hover {
      opacity: 0.7;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      bottom: -1.8rem;
      left: .4rem;
      width: 2rem;
      height: 2rem;
      border: .1rem solid $black;
      border-top: none;
      border-left: none;
      transform: rotate(45deg);
    }

    &::after {
      bottom: -2.8rem;
    }
  }

  &__poweredBy {
    justify-self: end;
    width: 18rem;
    margin: 0.8rem 6vw 0 0;
    fill: $gray2;
  }

  &__section-item {
    &.isMobile {
      display: none;
    }
  }

  &__section-title {
    margin: 0 0 7rem;
    font-size: $fz-xl;
    text-transform: uppercase;
  }

  &__section-subtitle {
    margin: 0 0 4rem;
    font-size: $fz-l;
  }

  &__section-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 9.5rem 13rem;
    max-width: 150rem;
    margin: 0 0 10rem;
  }
}

@media (max-width: 1680px) {
  .main {
    &__header-box {
      padding: 8.5rem 0 0 6.5rem;
    }

    &__header {
      font-size: 20.5rem;
    }

    &__subheader {
      font-size: 13.6rem;
      line-height: 16rem;
    }

    &__year {
      top: -3.5rem;
      right: -2rem;
      padding: 0;
      font-size: 36.3rem;
      line-height: 32.5rem;
    }

    &__map {
      top: 0.5rem;
      right: -15.5rem;
      max-width: 99rem;
    }

    &__desc {
      padding: 2rem 0 0;
      font-size: 3.1rem;
    }

    &__poweredBy {
      margin: 1.8rem 0 0;
    }

    &__btn-nav {
      margin: 8px 0 0 6rem;
    }

    &__section-list {
      grid-gap: 8.5rem 10rem;
    }

    &__section-subtitle {
      margin: 0 0 2rem;
    }
  }
}

@media (max-width: 767px) {
  .main {
    padding: 0 2rem;

    &__map {
      top: 19.5rem;
      right: -41rem;
      max-width: 80rem;
    }

    &__upper {
      grid-template-columns: 1fr;
    }

    &__lower {
      margin: 45rem 0 0;
    }

    &__header-box {
      grid-row-start: 1;
      padding: 0;
    }

    &__header {
      font-size: 11.1rem;
    }

    &__subheader {
      position: relative;
      padding: 0 0 1.8rem;
      font-size: 7.3rem;
      line-height: 1;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 27rem;
        height: .2rem;
        background-color: $black;
      }
    }

    &__desc {
      padding: 1rem 0 0;
      font-size: 2.5rem;
    }

    &__year {
      position: absolute;
      top: 24rem;
      right: 4rem;
      grid-column-start: 1;
      font-size: 19.1rem;
      line-height: 1;
    }

    &__section-title {
      margin: 0 0 3rem;
      font-size: 2rem;
      line-height: 1.25;
    }

    &__section-subtitle {
      margin: 0;
      font-size: 1.8rem;
    }

    &__section-list {
      grid-template-columns: repeat(2, minmax(10rem, 50%));
      width: 100%;
      grid-gap: 1.5rem 3.5rem;
      margin: 0 0 7rem;
    }

    &__section-item {
      &.isMobile {
        display: block;
      }
    }

    &__btn-nav {
      position: absolute;
      z-index: 2;
      top: 62rem;
      left: 9rem;
      font-size: 1.5rem;

      &::before,
      &::after {
        left: 3.5rem;
      }
    }
  }
}