.simpleTable {
  &__tableRow {
    display: grid;
    grid-template-columns: 4fr 1fr 15%;
    align-items: center;

    &--title {
      border-bottom: .1rem solid $purple;
    }

    &--footer {
      margin: 0 0 2rem;
      border-top: .1rem solid $gray6;
      border-bottom: .1rem solid $gray6;
    }
  }

  &__cell {
    font-size: $fz-l;
    line-height: 3rem;
    justify-self: center;
    padding: 1rem;

    &--wide {
      text-align: center;
    }
  }

  &__cellName,
  &__cellValue,
  &__cellShift {
    padding: 1rem 0;

    .chartDesc__tableRow--footer & {
      font-weight: 700;
    }
  }

  &__cellName {
    font-size: $fz-s;
    line-height: 3rem;
  }

  &__cellValue {
    justify-self: center;
  }

  &__cellShift {
    justify-self: center;
    color: $red2;

    &.plus {
      color: $green;
    }

    &--none {
      color: $black3;
    }
  }
}