.chartDesc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &__title {
    margin: 0 0 1.5rem;
    line-height: 2.4rem;
  }

  &__value,
  &__shift {
    font-size: $fz-xxl;
    line-height: 5.5rem;
  }

  &__shift {
    margin: 0 0 2rem;
  }

  &__measurement {
    font-size: $fz-s;
    line-height: 2rem;
  }

  &__button {
    margin: 2rem 0 0;
  }

  &__modal {
    color: $black3;
  }
}

@media (max-width: 767px) {
  .chartDesc {
    display: block;

    &__title,
    &__measurement {
      font-size: 1.2rem;
    }

    &__title {
      margin: 0 0 0.5rem;
    }

    &__shift,
    &__value {
      font-size: 2.5rem;
      line-height: 1;
    }

    &__shift {
      margin: 0 3.5rem 0 0;
    }

    &__value {
      margin: 0 .5rem 0 0;
    }

    &__button.button {
      display: none;
    }
  }
}