.subheader {
  position: fixed;
  z-index: 50;
  top: 19rem;
  left: 0;
  right: 0;
  padding: 1rem;
  margin: 0 auto;
  pointer-events: none;
  opacity: 0;
  transition: opacity $eia200;
  backdrop-filter: blur(5rem);
  background-color: rgba($white, .8);
  box-shadow: 0 0 1rem 0 $gray;

  &.visible {
    opacity: 1;
    pointer-events: all;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    max-width: 120rem;
    margin: 0 auto;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
  }

  &__desc {
    margin: 0 3rem 0 0;
    font-weight: 700;
  }

  &__toggle {
    position: relative;
    width: 1.5rem;
    height: 1.5rem;
    margin: 0 0 0 3rem;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: .9rem;
      width: .2rem;
      height: 1.5rem;
      border-right: .2rem solid $black;
      transform-origin: center center;
      transform: rotate(45deg);
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 1rem;
      width: .2rem;
      height: 1.5rem;
      border-left: .2rem solid $black;
      transform-origin: center center;
      transform: rotate(-45deg);
    }
  }

  &__value {
    font-size: 1.8rem;
    transition: color $eia200;

    &:hover,
    &.active {
      color: $purple;
    }
  }
}

@media (max-width: 1680px) {
  .subheader {
    top: 15rem;
  }
}

@media (max-width: 767px) {
  .subheader {
    display: none;
  }
}