.fieldOverview {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 192rem;
  min-height: 93rem;
  margin: 0 auto;
  overflow: hidden;

  .gasPage & {
    margin: 0 auto 8.5rem;
  }

  &__bg {
    position: absolute;
    z-index: 0;
    top: -12rem;
    left: 50%;
    width: 194rem;
    object-fit: cover;
    transform: translateX(-50%);
  }

  &__toggle {
    margin: 0 0 4.2rem;
  }

  &__content {
    position: relative;
    z-index: 1;
    flex-grow: 1;
    display: grid;
    grid-template-columns: 42% 1fr;
    width: 100%;
    max-width: 185rem;
    min-height: 100%;
    padding: 0 3rem 14rem;
    margin: 0 auto;
  }

  &__title {
    position: relative;
    padding: 30rem 2rem 30rem 7.5rem;
    color: $black2;
    text-transform: uppercase;
    backdrop-filter: blur(1.5rem);

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba($white, .4);
    }
  }

  &__title-main {
    position: relative;
    z-index: 1;
    font-size: 13rem;
    letter-spacing: 0.156rem;
    margin: 0 0 1.4rem;
  }

  &__title-sub {
    position: relative;
    z-index: 1;
    font-size: $fz-xxxl;
    line-height: 8.7rem;
    letter-spacing: 0.44rem;
    font-weight: 400;
  }

  &__inner {
    position: relative;
    padding: 23.5rem 10rem 10rem 15rem;
    backdrop-filter: blur(3rem);
    border-bottom-right-radius: 3rem;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba($white, .4);
      border-bottom-right-radius: 3rem;
    }
  }

  &__rate-list {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template: auto auto/auto auto;
    grid-gap: 3.5rem 4rem;
    color: $black2;
  }

  &__rate-title {
    font-size: $fz-m;
    letter-spacing: .135rem;
    text-transform: uppercase;
  }

  &__rate-value {
    font-size: $fz-xxxl;
    line-height: 5rem;
    letter-spacing: .413rem;
  }

  &__rate-measurement {
    font-size: $fz-s;
    line-height: 6.7rem;
  }

  &__slider {
    position: relative;
    z-index: 1;
  }

  &__sliderButton {
    display: none;
  }

  &__slider-toggle {
    width: 1.4rem;
    height: 1.4rem;
    margin: 0 0 3rem;
    border-radius: 50%;
    background-color: transparent;
    border: .1rem solid $black;
    transition: background-color $eia200;

    &:not(:last-of-type) {
      margin: 0 1.2rem 0 0;
    }

    &.active {
      background-color: $black;
    }
  }

  &__slider-list-wrapper {
    display: none;
    max-height: 35rem;
    padding: 0 3.5rem 0 0;
    font-size: $fz-s;
    overflow: auto;

    &.active {
      display: block;
    }
  }

  &__slider-text,
  &__slider-list-item {
    margin: 0 0 3rem;
    line-height: 1.4;
  }

  &__slider-list-item {
    position: relative;
    padding: 0 0 0 1.2rem;

    &::before {
      content: '';
      position: absolute;
      top: .8rem;
      left: 0;
      width: .5rem;
      height: .5rem;
      background-color: $black;
    }
  }
}

@media (max-width: 1680px) {
  .fieldOverview {
    &__content {
      grid-template-columns: 46% 1fr;
    }

    &__inner {
      padding: 13rem 10rem 10rem 10rem;
    }

    &__rate-list {
      grid-template-columns: auto;
    }

    &__title-main {
      font-size: 9rem;
    }

    &__title-sub {
      font-size: 4.2rem;
      line-height: 3.7rem;
    }

    &__rate-value {
      font-size: 5.5rem;
    }

    &__slider-list-wrapper {
      max-height: 30rem;
    }

    &__slider-text,
    &__slider-list-item {
      font-size: $fz-m;
    }
  }
}

@media (max-width: 767px) {
  .fieldOverview {
    width: 100%;
    min-height: 75rem;

    .gasPage & {
      margin: 0 auto 5rem;
    }

    &__title {
      padding: 3rem 2rem;
    }

    &__title-main {
      margin: 0 0 .5rem;
      font-size: 5.8rem;
    }

    &__title-sub {
      font-size: 2.4rem;
    }

    &__content {
      grid-template-columns: 1fr;
      grid-template-rows: 18rem 1fr;
      padding: 0 0 0 .5rem;
      margin: 0 0 5rem;
    }

    &__toggle {
      margin: 0 0 5rem;
    }

    &__rate-list {
      grid-gap: 0.5rem 4rem;
      padding: 0 0 0 3.5rem;
    }

    &__rate-title {
      margin: 0 0 -1rem;
      font-size: 1.4rem;
    }

    &__rate-value {
      font-size: 2.5rem;
    }

    &__rate-measurement {
      font-size: 1.4rem;
    }

    &__inner {
      padding: 0 2rem 2rem;
      backdrop-filter: blur(1.5rem);
      border-radius: 0 0 0 1rem;

      &::after {
        border-radius: 0 0 0 1rem;
      }
    }

    &__slider-text {
      font-size: 1.2rem;
    }

    &__slider-list-wrapper {
      width: 100%;
      padding: 0 3.5rem;
    }

    &__slider-toggle {
      display: none;
    }

    &__sliderButton {
      content: '';
      position: absolute;
      top: 5rem;
      left: 0;
      display: block;
      width: 1.5rem;
      height: 1.5rem;
      border: .1rem solid $black;
      border-top: none;
      border-left: none;
      transform: rotate(135deg);

      &--next {
        left: auto;
        right: 0;
        transform: rotate(-45deg);
      }
    }

    &__slider-list-item {
      font-size: 1.2rem;
    }
  }
}