.complexTable {
  &__row {
    display: grid;
    grid-template-columns: 1fr repeat(4, 17%);
    align-items: center;
    font-size: $fz-s;


    &--header {
      border-bottom: .1rem solid $blue2;
    }

    &--large {
      font-size: $fz-m;
    }

    &--top {
      border-top: .1rem solid $gray7;
    }

    &--bottom {
      border-bottom: .1rem solid $gray7;
    }
  }

  &__cell {
    padding: .8rem;
    line-height: 3rem;

    &:not(:first-of-type) {
      width: 100%;
      text-align: center;
      border-left: .1rem solid $gray7;
    }

    &--red {
      color: $red2;
    }

    &--green {
      color: $green;
    }
  }
}