.button {
  display: inline-block;
  vertical-align: middle;
  height: 2.8rem;
  padding: 0 2rem;
  text-align: center;
  color: white;
  font-size: $fz-s;
  line-height: 2.8rem;
  background-color: $black;
  border-radius: .5rem;
  transition: opacity $eia250;

  &:hover {
    opacity: .7;
  }

  &.gray {
    background-color: $gray4;
    color: $black;
  }

  > svg {
    vertical-align: middle;
    margin: 0 1rem 0 0;
  }
}