.barVertChart {
  display: grid;
  grid-template-columns: 80rem 1fr;

  &__desc {
    padding: 2rem 0 0 5rem;
  }
}

@media (max-width: 767px) {
  .barVertChart {
    grid-template-columns: 1fr;
    margin: 0 -2.5rem;

    &__chart-box {
      width: 100vw;
      height: 20rem;
    }

    &__desc {
      grid-row-start: 1;
      padding: 0 2.5rem;
    }
  }
}